.footer {
    background-color: #000;
    color: #fff;
    padding-bottom: 3rem;
}

.footer-address {
    padding: 4rem 0;
    border-bottom: 1px solid #ddd;
}

.address-info {
    display: flex;
    align-items: center;
}

.address-info box-icon {
    width: 35px;
    height: 35px;
    margin-right: 1rem;
}

.address-info {
    font-size: .9rem;
}

.footer-nav {
    padding-top: 3rem;
}

.footer-nav-item h3 {
    margin-bottom: 1.5rem;
    font-weight: 600;
    font-size: 1.2rem;
}

.item-nav li {
    margin-bottom: .5rem;
}

.item-nav li a {
    display: inline-block;
    color: #aaa;
    transition: .2s ease-in;
}

.item-nav li a:hover {
    color: #ffb900;
}

.item-nav box-icon {
    width: 30px;
    height: 30px;
    margin-right: 1rem;
}

.item-nav li a {
    display: flex;
    align-items: center;
}

@media (max-width: 992px) {
    .address-info {
        margin-bottom: 1rem;
    }
    .footer-nav-item {
        margin-bottom: 2rem;
    }
}
.breadcrumb-blog__title {
    display: inline-block;
    padding: 1rem;
}

.blog__content__title h1 {
    margin: 3rem 0;
    color: tomato;
    font-size: 2rem;
}

.blog__content__avatar {
    margin-bottom: 2rem;
}

.blog__content__avatar img {
    width: 100%;
    max-width: 100%;
    display: block;
}

.blog__content__description p {
    margin-bottom: 1rem;
    font-size: 15px;
    color: #555;
    line-height: 1.8;
}

.cart-product {
    padding: 5rem;
}

.cart-table {
    width: 100%;
}

.cart-table thead tr {
    background: #2c3e50;
    color: #fff;

}

.cart-table thead tr th {
    padding: .5rem;
}

.cart-table tbody tr td {
    padding: 1rem;
}

.cart-table tbody tr td.prod-thumbnail {
    width: 150px;
}

.cart-table tbody tr td.prod-thumbnail img {
    width: 100%;
    display: block;
}

.cart-table tbody tr td.prod-quantity div:first-of-type {
    display: flex;
    align-items: center;
}

.cart-table tbody tr td.prod-quantity span {
    padding: 1rem;
}

.cart-table tbody tr td.prod-quantity div.btn-change-quantity {
    display: flex;
    flex-direction: column; 
}

.cart-table tbody tr td.prod-quantity div.btn-change-quantity button {
    border: none;
    display: inline-block;
    padding: .8rem;
    font-family: 'Poppins';
    width: 100%;
    cursor: pointer;
}

.cart-table tbody tr td.prod-remove {
    text-align: center;
    cursor: pointer;
}

/* cart total */
.cart-total {
    background: #ecf0f1;
}

h3.cart-total_title {
    font-weight: 600;
    padding: 1rem;
    border-bottom: 1px solid #ddd;
}

.cart-total_info {
    padding: 1rem;
}

.subtotla {
    display: flex;
    padding-bottom: 1rem;
}

.subtotla span:first-child {
    width: 40%;
}

.subtotla span:last-child {
    width: 60%;
}

.shipping {
    padding-bottom: 1rem;
}

.shipping_title {
    padding-bottom: .5rem;
}

.shipping-content {
    padding-left: 2rem;
}

.shipping-content div label {
    cursor: pointer;
}

.shipping-content input {
    margin-right: .8rem;
    cursor: pointer;
}

.free-shipping {
    padding-bottom: .5rem;
}

.total {
    display: flex;
}

.total span:first-child {
    width: 40%;
}

.total span:last-child {
    width: 60%;
    font-weight: 600;
    color: #e74c3c;
}

/* when no product in cart */

.no-product-in-cart p {
    padding: .5rem 1rem;
    background: #fff4bb;
    color: #e99b00;
    border-color: #ffcc66;
    margin: 1rem 0;
}

.no-product-in-cart a {
    display: inline-block;
    padding: .5rem 1rem;
    color: #fff;
    background: #2d3436;
    border: 2px solid #2d3436;
    transition: .2s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.no-product-in-cart a:hover {
    color: #2d3436;
    background: #fff;
}

@media (max-width: 992px) {
    .cart-product {
        padding: 5rem 0;
    }
    .cart-product .container {
        padding: 0;
        max-width: 720px;
    }
    .cart-table {
    margin-bottom: 3rem;
    }   
    .cart-table tbody tr td {
        padding: 0.5rem 0 0 1rem;
    }
    .cart-table tbody tr td:nth-child(1) {
        padding-left: 0;
    }

}

@media (max-width: 576px) {
    .cart-table {
        padding: 0 .75rem;
    }
    .cart-table thead {
        display: none;
    }
    .cart-table tbody tr {
        display: block;
        text-align: center;
        border: 1px solid #ddd;
        margin-bottom: 1rem;
    }
    .cart-table tbody tr td {
        display: block;
        padding: 1rem;
    }

    .cart-table tbody tr td.prod-thumbnail {
        margin: 0 auto;
    }
    .cart-table tbody tr td.prod-quantity .quantity-content {
        justify-content: center;
    }
    .cart-table tbody tr td.prod-quantity div.btn-change-quantity {
        flex-direction: row;
    }
    .cart-table tbody tr td.prod-total {
        font-weight: 600;
    }
}
.breadcrumb {
    font-size: 1rem;
    color: #555;
    text-align: right;
    border-bottom: 1px solid #ddd;
}

.breadcrumb_home, .breadcrumb_shop, .breadcrumb span {
    display: inline-block;
    margin: 1rem 1rem;
    color: #555;
    transition: .2s ease-out;
}

.breadcrumb a:hover {
    color: #ffb900;
}

@media (max-width: 576px) {
    .breadcrumb_home, .breadcrumb_shop, .breadcrumb span {
        margin: .5rem;
    }
}
.nav-tab-show {
    border: 1px solid #ddd;
    margin-bottom: 1rem;
}

ul.tabs-show {
    display: flex;
}

ul.tabs-show li {
    padding: .8rem 2rem;
    margin: 0.6rem;
    cursor: pointer;
    color: #000;
    border: 1px solid #ddd;
    transition: all .2s linear;
    font-weight: 500;
}

ul.tabs-show li:hover {
    background-color: #ffb900;
    color: #fff;
}

ul.tabs-show .tabs-show__item.active {
    background-color: #ffb900;
    color: #fff;
}

/* products */

.product-avatar a {
    overflow: hidden;
    display: block;
}

.product-avatar a img {
    display: block;
    width: 100%;
    max-width: 100%;
    transition: .2s ease;
}

.product-info {
    text-align: center;
    padding: 1rem;
}

.product-info__title a {
    display: inline-block;
    color: #555;
    font-size: 1rem;
    margin-bottom: .5rem;
    transition: .2s ease-in-out;
    font-weight: 400;
}

.product-info__title a:hover {
    color: tomato;
}

.product-info__price {
    color: #000;
    font-size: 1.2rem;
    font-weight: 600;
    display: block;
    margin-bottom: .5rem;
}

.product-info__btn-atc {
    border: 0 solid;
    outline: none;
    padding: .7rem 1rem;
    font-family: 'Poppins',sans-serif;
    color: #fff;
    background-color: tomato;
    border: 3px solid tomato;
    display: block;
    width: 100%;
    visibility: hidden;
    font-weight: 600;
    cursor: pointer;
}

.product-info__btn-atc:hover {
    background-color: #fff;
    color: tomato;
}

.card-product:hover .product-info__btn-atc {
    transition: .2s linear;
    visibility: visible;
}

.card-product:hover .product-avatar img{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

/* Banner Product */

.banner-product {
    display: flex;
}

.wrap-banner-product {
    position: relative;
}

.wrap-banner-product a {
    display: block;
}

.wrap-banner-product img {
    display: block;
    width: 100%;
}

.wrap-banner-product:nth-child(1) span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: .5;
    transition: .2s linear;
}

.wrap-banner-product:nth-child(1) span:hover {
    background-color: #f72e33;
}

.wrap-banner-product:nth-child(3) span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: .5;
    transition: .2s linear;
}

.wrap-banner-product:nth-child(3) span:hover {
    background-color: #000;
}

.wrap-banner-product:nth-child(2) span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: .5;
    transition: .2s linear;
    border: 0px solid #eee;
}

.wrap-banner-product:nth-child(2) span:hover {
    border: 10px solid #eee;
}

@media (max-width: 768px) {
    ul.tabs-show {
        justify-content: space-around;
    }
    ul.tabs-show li {
        padding: .4rem 1rem;
        margin: 0.4rem;
        font-size: .9rem;
    } 
    .product-info__btn-atc {
        visibility: visible;
    } 
}

@media (max-width: 576px) {
    .product-info__title a {
        font-size: .9;
    }
    .product-info__price {
        font-size: 1rem;
    }
    .product-info__btn-atc {
        visibility: visible;
    }
}



