.filter__sort {
    width: 230px;
    padding: .5rem;
    border: 1px solid #ddd;
    position: relative;
    display: flex;
    align-items: center;
    transition: .2s ease-in;
    cursor: pointer;
}

.filter__sort:hover {
    color: #fff;
    background-color: #555;
}

.filter__sort ul {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: #fff;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    transition: .2s linear;
}

